@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500&display=swap');

body{
  font-family: 'Poppins', sans-serif;
  overflow-y: hidden;
}
.nav {
  height: 50px;
  border-radius:10px;
  background-color: #404040;
  position: relative;
  padding:10px;
  font-family: 'Poppins', sans-serif;
}

.nav > .nav-header {
  display: inline;
}

.nav > .nav-header > .nav-title {
  display: inline-block;
  font-size: 22px;
  color: #fff;
  padding: 10px 10px 10px 30px;
}

.nav > .nav-btn {
  display: none;
}

.nav > .nav-links {
  display: inline;
  float: right;
  font-size: 18px;
}

.nav > .nav-links > a {
  display: inline-block;
  padding: 13px;
  text-decoration: none;
  color: #efefef;
}

.nav > .nav-links > a:hover {
  background-color: rgba(0, 0, 0, 0.3);
  border-radius:10px;
}

.nav > #nav-check {
  display: none;
}

.hidden-drp-down .css-b62m3t-container{
  display:none;
}

@media (max-width:840px) {
  .nav > .nav-btn {
    display: inline-block;
    position: absolute;
    right: 0px;
    top: 8px;
  }

  body{
    overflow: auto;
  }

  .sidebar{
    display: none;
  }

  .hidden-drp-down .css-b62m3t-container{
    display:block;
  }

  .nav > .nav-btn > label {
    display: inline-block;
    width: 50px;
    border-radius:10px;
    padding: 13px;
  }
  .nav > .nav-btn > label:hover,.nav  #nav-check:checked ~ .nav-btn > label {
    background-color: rgba(0, 0, 0, 0.3);
  }
  .nav > .nav-btn > label > span {
    display: block;
    width: 25px;
    height: 10px;
    border-top: 2px solid #eee;
  }
  .nav > .nav-links {
    position: absolute;
    display: block;
    width: 100%;
    background-color: #404040;
    height: 0px;
    transition: all 0.3s ease-in;
    overflow-x: hidden;
    top: 75px;
    left: 0px;
    border-radius:10px;
    z-index: 100;
  }
  .nav > .nav-links > a {
    display: block;
    width: 100%;
  }
  .nav > #nav-check:not(:checked) ~ .nav-links {
    height: 0px;
  }
  .nav > #nav-check:checked ~ .nav-links {
    height: calc(35vh - 50px);
    overflow-y: auto;
  }
}