
.request form {
  display: flex;
  flex-wrap: wrap
}

.request form > input {
    flex: 1 1 10ch;
    margin: .5rem
  }

.request form > input[type="email"] {
      flex: 1 1 30ch;
      font-family: 'Poppins';
      font-size: 16px;
    }

.request input {
  border: none;
  background: hsl(0, 0%, 93%);
  border-radius: .25rem;
  padding: .75rem 1rem;
  font-size: 16px;
  font-family: 'Poppins';
}

.request input[type="submit"] {
  font-family: 'Poppins';
    background: #141E61;
    color: white;
  }

  .listItems{
    display: flex;
    justify-content: center;
    margin-top:1rem;
  }

  .requestListItem{
    width: 90%;
    display: flex;
    flex-direction: row;
    background-color: #f3f3f3;
    justify-content: space-between;
    padding: 10px 20px;
    border-radius: 10px;
    flex-wrap: wrap;
    gap:1rem;
  }
  .courseInfo{
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 2rem;
    flex-wrap: wrap;

  }
  .likesection{
    display: flex;
    gap: 1rem;
  }

  .requestListItem .name,
  .requestListItem .courseName{
    font-size: 18px;
    font-weight: 700;
  }

  .requestListItem .email, 
  .requestListItem .courseLink{
    width:20rem;
    font-size:15px;
    font-weight:500;
    color: #4f4f4f;
    word-wrap:break-word;
  }